import {ActionsTypes} from "./store";

const FOLLOW = 'FOLLOW'
const UNFOLLOW = 'UNFOLLOW'
const SET_USERS = 'SET-USERS'

export type StateType = {
    users: Array<UserType>
}

export type UserType = {
    followed: boolean
    id: number
    name: string
    location: UserLocationType
    status: string
    photos: userPhotoType
}

type userPhotoType = {
    small: string | undefined
    large: string | undefined
}

type UserLocationType = {
    country: string
    city: string
}


let initialState = {
    users: [
        // {
        //     followed: true,
        //     personPhoto: 'data:image/jpeg;base64,',
        //     id: 1,
        //     fullName: 'Dmitry',
        //     status: 'Hi man!',
        //     location: {country: 'Ukraine', city: 'Kharkov'}
        // },
        // {
        //     followed: false,
        //     personPhoto: 'data:image/jpeg;base64,',
        //     id: 2,
        //     fullName: 'Dimas',
        //     status: 'How are you?',
        //     location: {country: 'Russia', city: 'Orel'}
        // },
        // {
        //     followed: true,
        //     personPhoto: 'https://encrypted-tbn0.gstatic.com/images?q=tbn%3AANd9GcT88Xel22e95INYZ8zhxrKj8ojy_HuGh1Kluw&usqp=CAU',
        //     id: 3,
        //     fullName: 'Dimon',
        //     status: 'Dont you sleep? ',
        //     location: {country: 'Poland', city: 'Krakov'}
        // },
    ]
}

const UsersReducer = (state: StateType = initialState, action: ActionsTypes): StateType => {
    switch (action.type) {
        case "FOLLOW":
            return {
                ...state,
                users: state.users.map((u) => {
                    if (u.id === action.userId) {
                        return {...u, followed: true}
                    }
                    return {...u}
                })
            }
        case 'UNFOLLOW':
            return {
                ...state,
                users: state.users.map((u) => {
                    if (u.id === action.userId) {
                        return {...u, followed: false}
                    }
                    return {...u}
                })
            }
        case 'SET-USERS':
            return {
                ...state, users: [...state.users, ...action.users]
            }
        default:
            return state
    }
}

export const FollowAC = (userId: number) => {
    return {type: FOLLOW, userId, followed:true} as const
}
export const UnfollowAC = (userId: number) => {
    return {type: UNFOLLOW, userId, followed:false} as const
}
export const SetUsersAC = (users: Array<UserType>) => {
    return {type: SET_USERS, users} as const
}

export default UsersReducer